import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {EstacionservicioIndexComponent} from './../estacionservicio/estacionservicio-index/estacionservicio-index.component';
import {EstacionservicioAddComponent} from './../estacionservicio/estacionservicio-add/estacionservicio-add.component';
import {EstacionservicioEditComponent} from './../estacionservicio/estacionservicio-edit/estacionservicio-edit.component';
import { Candeactivateguard } from '../shared/can-deactivate-guard/can-deactivate-guard.service';

const routes: Routes = [ { path: '', component: EstacionservicioIndexComponent},
{ path: 'add', component: EstacionservicioAddComponent, canDeactivate: [Candeactivateguard]},
{ path: 'edit/:id', component: EstacionservicioEditComponent, canDeactivate: [Candeactivateguard]}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EstacionservicioRoutingModule { }



