  typeaheadNoResults: boolean;
  dataSource: Observable<any>;
  composelect: Componente;
  complementopk: -1;
  @ViewChild('gmap') gmapElement: any;
  lat = 40.50017;
  long = -3.89239;
  map: google.maps.Map;
  markers = [];
  public uploader: FileUploader = new FileUploader({
    url: environment.apiUrl + 'api/archivoincidencia',
    authTokenHeader: "Authorization",
    authToken: "Bearer " + localStorage.getItem('access_token'),
    isHTML5: true,
    autoUpload: true,
    maxFileSize: 5 * 1024 * 1024,

  });
  apiurl: any;
  public hasBaseDropZoneOver: boolean = false;
  public hasAnotherDropZoneOver: boolean = false;

  @ViewChild('editSwal') private editSwal: SwalComponent;
  @ViewChild('createSwal') private createSwal: SwalComponent;
  @ViewChild('contratoSwal') private contratoSwal: SwalComponent;
  @ViewChild('componentsForm') ngForm:NgForm
  obra: string;
  urlestacion: string;
  objparameter: { obra: number; };
  public validators = [this.ValidateEmail];

  public errorMessages = {
    'ValidateEmail': 'solo se admite email'
  };
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private estacionservicioService: EstacionservicioService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private zonaTecnicaService: ZonaTecnicaService,
    private tipocompoenenteService: TipocompoenenteService,
    private clienteService: ClienteService,
    private mapsService: MapsService,
    private candeactivateguard: Candeactivateguard,
    private archivouploadService: ArchivouploadService,

  ) {

    this.candeactivateguard.setedit(true);
    this.dataSource = Observable.create((observer: any) => {
      // Runs on every search
      this.clienteService.gettoken(this.item.razon_social).subscribe((value) => {
        observer.next(value.data)
      })
    });
    this.uploader.onAfterAddingFile = (file: any) => {
      this.uploader.options.additionalParameter = { estacion_id: 0, tipofotosiniestro: 0, tipo: 3 };
      file.withCredentials = false;
    };
    this.uploader.onErrorItem = (item, response, status, headers) => this.onErrorItem(item, response, status, headers);
    this.uploader.onSuccessItem = (item, response, status, headers) => this.onSuccessItem(item, response, status, headers);
    this.uploader.onWhenAddingFileFailed = (fileItem) => {
      this.notificationService.onError('La imagen no se pudo cargar, '
        + 'el tamaño maximo es de 5MB y el formato es png o jpg, INtentelo nuevamente');
    };
    this.apiurl = environment.apiUrl;


  }
  private ValidateEmail(control: FormControl) {
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(control.value)) {

      return null;
    }
    return {
      'ValidateEmail': true
    };

  }
  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.estacionservicioService.getById($id)
      .subscribe((response) => {
        this.item = response.data;
        this.urlestacion = 'estacionservicio';
        this.urlestacion += this.item.esobra != undefined && this.item.esobra == true ? '?obra=1' : '';
        this.objparameter = this.item.esobra != undefined && this.item.esobra == true ? { obra: 1 } : null;
        if (this.item.email != undefined) {
          this.item.emails = this.item.email.split(',');
        }

        let myLatlng = new google.maps.LatLng(this.item.lat, this.item.lng);
        this.addclickmarker(myLatlng);
        this.spinnerService.hide();
      })
  }

  changeTypeaheadLoading(e: boolean): void {

    this.typeaheadLoading = e;
  }

  typeaheadOnSelect(e: TypeaheadMatch): void {
    this.item.codigo_cliente = e.item.codigo_cliente;
    this.item.email = e.item.EMAIL;
  }

  ngOnInit() {
    this.spinnerService.show();
    this.newsubcompo = new Componente();
    this.newcompo = new Componente();
    this.newcompo.fk_tipocomponente = -1;
    this.newcompo.componentes = [];
    this.cargarZonas();
    this.cargartiposcomponente();
    this.item = new estacionservicio();
    this.item.fk_zonatecnica = "-1";
    this.item.razon_social = "";
    this.item.archivos = [];
    this.item.componentes = [];
    this.initmap();
    this.spinnerService.show();
    this.getById();
    console.log(this.item, "item on load")

  }

  cargarZonas() {

    this.zonaTecnicaService.get(1).subscribe((value) => {
      this.zonas = value.data;

    });

  }
  cargartiposcomponente() {

    this.tipocompoenenteService.get(0).subscribe((value) => {
      this.tiposcomponente = value.data;
      this.spinnerService.hide();


    });

  }
  addNewComponente() {
    this.item.componentes.push(this.newcompo);
    this.newcompo = new Componente();

    this.newcompo.fk_tipocomponente = -1;
    this.newcompo.componentes = [];
    this.newsubcompo = new Componente();
  }
  addNewsubComponente(row) {
    row.componentes.push(this.newsubcompo);
    this.newsubcompo = new Componente();
  }
  add() {
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    this.item.user_id = this.localDataService.getLocalData('userInfo').id;
    if (this.item.emails != undefined) {
      let emails = this.item.emails.map(function (email) {
        if (email.value != undefined) {
          return email.value;
        } else {
          return email;
        }

      });
      this.item.email = emails.join();

    }

    this.estacionservicioService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Estación de Servicio Actualizada.')
        this.router.navigateByUrl(this.urlestacion);
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Estacion de Servicio ya existe");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }
  deletecomponent(componente) {
    let index = this.item.componentes.indexOf(componente);
    this.item.componentes.splice(index, 1);

  }
  dactivarcomponent(componente) {
    componente.estado = true;

  }
  deleteSubcomponent(componente, subcomponente) {
    let parentComponent = componente.fk_fathercom;
    let componentId = componente.id;
    let selectedComponentId = this.composelect.id;
    /*  this.item.componentes.componentes[id] */
    console.log("componente:", componente, "subcomponente", subcomponente)
    //componente.splice(index, 1);

    console.log("prev data", this.item)
    /* let indexSubComponente = this.item.componentes.indexOf(componente);
    let index = componente.componentes.indexOf(subcomponente);
    console.log("componente:", componente, "subcomponente", subcomponente, "index:", index)
    componente.componentes.splice(index, 1); */

    //this.estacionservicioService.deleteComponent(componentId)
    this.item = this.deleteItemFromComponents(selectedComponentId, subcomponente, this.item);
    console.log(this.item, "updated item")

  }

