import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { EstacionservicioService } from "./../estacionservicio.service";
declare var $;

import "datatables.net";
import "datatables.net-bs4";
import "datatables.net-buttons";
import "datatables.net-buttons-bs4";
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-estacionservicio-index',
  templateUrl: './estacionservicio-index.component.html',
  styleUrls: ['./estacionservicio-index.component.css']
})
export class EstacionservicioIndexComponent implements OnInit {

  listaitem = [];
  table = null;
  obra: string;
  urladd: string;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private estacionservicioService: EstacionservicioService,
    private notificationService: NotificationService,
    private route: ActivatedRoute, ) { 
      this.urladd = '/estacionservicio/add';
    }

  ngOnInit() {
    this.spinnerService.show();

    this.obra = this.route.snapshot.queryParamMap.get('obra');
    
    if (this.obra != undefined) {
      this.urladd = '/estacionservicio/add?obra=1';
    }
    this.get();

  }
  get() {

    this.estacionservicioService.getbyobra(1, this.obra)
      .subscribe((response) => {
        this.listaitem = response.data;
        this.reInitDatatable();
        this.spinnerService.hide();

      });
  }
  loadtable() {
    let exampleId: any = $('#example23');

    this.table = exampleId.DataTable({
      dom: 'Bfrtip',
      buttons: [
        'copy', 'csv', 'excel', 'pdf', 'print'
      ],
      oLanguage: {
        "sLengthMenu": "_MENU_ registros por página",
        "sInfoFiltered": " - filtrada de _MAX_ registros",
        "sSearch": "Buscar: ",
        "sZeroRecords": "No existen datos diponibles",
        "sInfoEmpty": " ",
        "sInfo": 'Mostrando _END_ de _TOTAL_',
        "oPaginate": {
          "sPrevious": "Anterior",
          "sNext": "Siguiente"
        }
      }
    });
    $('.buttons-copy, .buttons-csv, .buttons-print, .buttons-pdf, .buttons-excel').addClass('btn btn-primary mr-1');
  }
  ngAfterViewInit() {



  }
  delete(id) {
    this.spinnerService.show();
    this.estacionservicioService.delete(id)
      .subscribe((value) => {
        if (value.data.estado) {
          this.notificationService.onSuccess('Estacion de Servicio Activada con Exito.')
        } else {
          this.notificationService.onSuccess('Estacion de Servicio Eliminada con Exito.');
        }

        this.get();
        this.spinnerService.hide();


      });
  }
  private reInitDatatable(): void {
    if (this.table) {
      this.table.destroy()
      this.table = null
    }
    setTimeout(() => this.loadtable(), 0)
  }
}



