import { tipocomponente } from './tipocomponente';
import { ComponenteActuacion } from './componente_actuacion';

export class Componente {
    id: number;
    codigo: string;
    descripcion: string;
    marca: string;
    modelo: string;
    numeroserie: string;
    idEstacion: number;
    fk_tipocomponente: number;
    tipocomponente: tipocomponente;
    componentes: Componente[];
    componentfields: any;
    select: boolean;
    actuaciones: ComponenteActuacion[];
    contratos: any;
    newsb: any;
    observacionescontrato: any;
    archivos: any;
    constructor() {
        this.componentes = [];
        this.actuaciones = [];
    }
}


