import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { EstacionservicioService } from "./../estacionservicio.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { LocalDataService } from './../../shared';
import { estacionservicio } from '../../viewmodel/estacionservicio';
import { ZonaTecnicaService } from '../../zonaTecnica/zona-tecnica.service';
import { TipocompoenenteService } from './../../tipocomponente/tipocompoenente.service';
import { ClienteService } from './../../clientes/cliente.service';
import { Componente } from '../../viewmodel/Componente';
import { Observable } from 'rxjs';
import { of } from 'rxjs/observable/of';
import { TypeaheadMatch } from 'ngx-bootstrap/typeahead';
import { mergeMap } from 'rxjs/operators';
import { tipocomponente } from '../../viewmodel/tipocomponente';
import { tipocomponenteadicional } from '../../viewmodel/tipocomponenteadicional';
declare var $;
import { } from '@types/googlemaps';
import { MapsService } from '../../google-apis/maps.service';
import { Candeactivateguard } from '../../shared/can-deactivate-guard/can-deactivate-guard.service';
import { FileUploader, FileLikeObject, FileItem, ParsedResponseHeaders } from 'ng2-file-upload';
import { environment } from '../../../environments/environment';
import { ArchivouploadService } from '../../archivoupload/archivoupload.service';
import { FormControl } from '@angular/forms';
@Component({
  selector: 'app-estacionservicio-add',
  templateUrl: './estacionservicio-add.component.html',
  styleUrls: ['./estacionservicio-add.component.css']
})
export class EstacionservicioAddComponent implements OnInit {
  idcompadicional = '-1';
  item: estacionservicio;
  zonas: any;
  newcompo: Componente;
  newsubcompo: Componente;
  tiposcomponente: any;
  asyncSelected: string;
  typeaheadLoading: boolean;
  _typeaheadNoResults: boolean;
  dataSource: Observable<any>;
  tipocomponentesel: tipocomponente;
  tipocomponenteadic: tipocomponente;
  @ViewChild('gmap') gmapElement: any;
  lat = 40.50017;
  long = -3.89239;
  map: google.maps.Map;
  markers = [];
  public uploader: FileUploader = new FileUploader({
    url: environment.apiUrl + 'api/archivoincidencia',
    authTokenHeader: "Authorization",
    authToken: "Bearer " + localStorage.getItem('access_token'),
    isHTML5: true,
    autoUpload: true,
    maxFileSize: 5 * 1024 * 1024,

  });
  apiurl: any;
  public hasBaseDropZoneOver: boolean = false;
  public hasAnotherDropZoneOver: boolean = false;
  obra: string;
  urlestacion: string;
  objparameter: { obra: number; };
  public validators = [this.ValidateEmail];

  public errorMessages = {
    'ValidateEmail': 'solo se admite email'
  };
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private estacionservicioService: EstacionservicioService,
    private router: Router,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private zonaTecnicaService: ZonaTecnicaService,
    private tipocompoenenteService: TipocompoenenteService,
    private clienteService: ClienteService,
    private mapsService: MapsService,
    private candeactivateguard: Candeactivateguard,
    private archivouploadService: ArchivouploadService,
    private route: ActivatedRoute,

  ) {
    this.obra = this.route.snapshot.queryParamMap.get('obra');
    this.candeactivateguard.setedit(true);
    this.dataSource = Observable.create((observer: any) => {
      // Runs on every search
      this.clienteService.gettoken(this.item.razon_social).subscribe((value) => {
        observer.next(value.data)

      })
    });
    this.uploader.onAfterAddingFile = (file: any) => {
      this.uploader.options.additionalParameter = { estacion_id: 0, tipofotosiniestro: 0, tipo: 3 };
      file.withCredentials = false;
    };
    this.uploader.onErrorItem = (item, response, status, headers) => this.onErrorItem(item, response, status, headers);
    this.uploader.onSuccessItem = (item, response, status, headers) => this.onSuccessItem(item, response, status, headers);
    this.uploader.onWhenAddingFileFailed = (fileItem) => {
      this.notificationService.onError('La imagen no se pudo cargar, '
        + 'el tamaño maximo es de 5MB y el formato es png o jpg, INtentelo nuevamente');
    };
    this.apiurl = environment.apiUrl;

  }

  typeaheadNoResults(event: boolean): void {
    this._typeaheadNoResults = event;
    this.item.razon_social = '';
  }
  changeTypeaheadLoading(e: boolean): void {

    this.typeaheadLoading = e;
  }

  typeaheadOnSelect(e: TypeaheadMatch): void {
    this.item.codigo_cliente = e.item.codigo_cliente;
  }

  ngOnInit() {
    this.spinnerService.show();
    this.newsubcompo = new Componente();
    this.newcompo = new Componente();
    this.newcompo.fk_tipocomponente = -1;
    this.newcompo.componentes = [];
    this.cargarZonas();
    this.cargartiposcomponente();
    this.item = new estacionservicio();
    this.item.fk_zonatecnica = "-1";
    this.item.razon_social = "";
    this.item.componentes = [];
    this.item.archivos = [];
    this.initmap();

    this.urlestacion = 'estacionservicio';
    this.urlestacion += this.obra != undefined && this.item.esobra == true ? '?obra=1' : '';
    this.objparameter = this.obra != undefined && this.item.esobra == true ? { obra: 1 } : null;
  }

  private ValidateEmail(control: FormControl) {
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(control.value)) {

      return null;
    }
    return {
      'ValidateEmail': true
    };

  }
  

  cargarZonas() {

    this.zonaTecnicaService.get(1).subscribe((value) => {
      this.zonas = value.data;

    });

  }
  cargartiposcomponente() {

    this.tipocompoenenteService.get(0).subscribe((value) => {
      this.tiposcomponente = value.data;
      this.spinnerService.hide();


    });

  }
  addNewComponente() {
    this.item.componentes.push(this.newcompo);
    this.tipocomponentesel = null;

    this.newcompo = new Componente();
    this.newcompo.fk_tipocomponente = -1;
    this.newcompo.componentes = [];
    this.newsubcompo = new Componente();
  }

  addNewComponenteAdic() {
    var _componenteadic = new Componente();
    _componenteadic.tipocomponente = JSON.parse(JSON.stringify(this.tipocomponenteadic));
    _componenteadic.fk_tipocomponente = this.tipocomponenteadic.id;
    this.newcompo.componentes.push(_componenteadic)
    this.tipocomponenteadic = new tipocomponente();
    this.idcompadicional = '-1';

  }

  add() {
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    this.item.user_id = this.localDataService.getLocalData('userInfo').id;
    this.item.esobra = this.obra == undefined ? false : true;
    this.item.estadoobra = 'PENDIENTE';
    let emails = this.item.emails.map(function (email) {
      if (email.value != undefined) {
        return email.value;
      } else {
        return email;
      }

    });
    this.item.email = emails.join();
    this.estacionservicioService.add(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Estacion de Servicio agregada.');

        this.router.navigateByUrl(this.urlestacion);
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Estacion de Servicio ya existe");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }
  deletecomponent(componente) {
    let index = this.item.componentes.indexOf(componente);
    this.item.componentes.splice(index, 1);

  }
  deleteSubcomponent(componente, subcomponente) {
    let index = componente.componentes.indexOf(subcomponente);
    componente.componentes.splice(index, 1);

  }
  getComponenteById($id) {
    this.spinnerService.show();

    this.tipocompoenenteService.getById($id)
      .subscribe((response) => {
        this.newcompo.tipocomponente = response.data

        for (var i = 0; i < this.newcompo.tipocomponente.componentesfields.length; i++) {
          if (this.newcompo.tipocomponente.componentesfields[i].tipo == 'Lista') {
            this.newcompo.tipocomponente.componentesfields[i].result = '-1';

          } else {
            this.newcompo.tipocomponente.componentesfields[i].result = '';
          }
        }
        this.spinnerService.hide();
      })
  }
  buscarcomponente() {
    this.getComponenteById(this.newcompo.fk_tipocomponente);
  }

  buscarcomponenteadicional() {

    var result = this.newcompo.tipocomponente.componentesadicional.filter(comp => comp.fk_tipocomponenteadicional.toString() == this.idcompadicional);
    if (result.length > 0) {
      this.tipocomponenteadic = result[0].tipocomponente;
    }
  }
  delete(row) {
    var index = this.item.componentes.indexOf(row);
    this.item.componentes.splice(index, 1);
  }
  initmap() {

    let myLatlng = new google.maps.LatLng(this.lat, this.long);
    let mapProp = {
      center: myLatlng,
      zoom: 15,
      mapTypeId: google.maps.MapTypeId.ROADMAP,

    };
    this.markers = [];
    this.map = new google.maps.Map(this.gmapElement.nativeElement, mapProp);
    google.maps.event.addListener(this.map, 'click', (event) => {
      let myLatLng = event.latLng;
      let lat = myLatLng.lat();
      let lng = myLatLng.lng();
      this.item.lat = lat;
      this.item.lng = lng;
      this.addclickmarker(myLatLng);


    });
  }
  addclickmarker(myLatlng) {
    let marker = new google.maps.Marker({
      position: myLatlng,
      map: this.map,
      animation: google.maps.Animation.DROP,
      title: 'Hola!',
      label: 'D'
    });
    this.setMapOnAll(null);
    this.markers.push(marker);
  }
  addmarker() {

    this.setMapOnAll(null);
    this.mapsService.codeAddress(this.item.direccion)
      .subscribe((response) => {
        if (response.length > 0) {
          this.item.lat = response[0].geometry.location.lat();
          this.item.lng = response[0].geometry.location.lng();
          let myLatlng = new google.maps.LatLng(this.item.lat, this.item.lng);

          let marker = new google.maps.Marker({
            position: myLatlng,
            map: this.map,
            animation: google.maps.Animation.DROP,
            title: 'Hola!',
            label: this.item.razon_social
          });
          this.map.setCenter(marker.getPosition());
          this.markers.push(marker);
        }

      })

  }
  setMapOnAll(map) {
    for (let i = 0; i < this.markers.length; i++) {
      this.markers[i].setMap(map);
    }
    this.markers = [];
  }
  onErrorItem(item: FileItem, response: string, status: number, headers: ParsedResponseHeaders): any {
    let error = JSON.parse(response); //error server response
    console.log(error);
  }

  public fileOverBase(e: any): void {

    this.uploader.options.additionalParameter = { estacion_id: 0, tipofotosiniestro: 0, tipo: 3 };
    this.hasBaseDropZoneOver = e;
  }

  public fileOverAnother(e: any): void {
    this.uploader.options.additionalParameter = { estacion_id: 0, tipofotosiniestro: 0, tipo: 3 };
    this.hasAnotherDropZoneOver = e;
  }
  onSuccessItem(item: FileItem, response: string, status: number, headers: ParsedResponseHeaders): any {
    if (this.item.archivos == undefined || this.item.archivos == null) {
      this.item.archivos = [];
    }
    this.item.archivos.push($.parseJSON(response).data);
  }
  eliminar_imagen(imagen) {
    this.archivouploadService.delete(imagen.id, 3).subscribe((value) => {
      let indice = this.item.archivos.indexOf(imagen);
      this.item.archivos.splice(indice, 1);
      this.notificationService.onSuccess('Imagen eliminada con exito.');
    });
  }




}



