import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
/*modules */
// import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { AuthenticationModule } from './../shared';
import { EnvironmentModule } from './../shared';
import { HttpModule } from '@angular/http';
import { FormValidationModule } from './../shared';
import { NotificationModule } from './../shared';
import { LocalDataModule } from './../shared';
import { AlertModule } from 'ngx-bootstrap/alert';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { SnotifyModule, SnotifyService, ToastDefaults } from 'ng-snotify';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';
/*components */
import { EstacionservicioRoutingModule } from './estacionservicio-routing.module';
import { EstacionservicioIndexComponent } from './../estacionservicio/estacionservicio-index/estacionservicio-index.component';
import { EstacionservicioAddComponent } from './../estacionservicio/estacionservicio-add/estacionservicio-add.component';
import { EstacionservicioEditComponent } from './../estacionservicio/estacionservicio-edit/estacionservicio-edit.component';
import { EstacionservicioService } from './estacionservicio.service';
import { RequestInterceptor } from '../shared/interceptor/http_interceptor';
import { MapsService } from '../google-apis/maps.service';
import { ContratosModule } from '../contratos/contratos.module';
import { Candeactivateguard } from '../shared/can-deactivate-guard/can-deactivate-guard.service';
import {FileUploadModule} from 'ng2-file-upload';
import { TagInputModule } from 'ngx-chips';
@NgModule({
  imports: [
    CommonModule,
    EstacionservicioRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    // Ng4LoadingSpinnerModule.forRoot(),
    AlertModule.forRoot(),
    SweetAlert2Module.forRoot(),
    SnotifyModule,
    HttpModule,
    AuthenticationModule,
    EnvironmentModule,
    FormValidationModule,
    NotificationModule,
    LocalDataModule,
    TypeaheadModule.forRoot(),
    ContratosModule,
    FileUploadModule,
    TagInputModule,
  ],
  declarations: [
    EstacionservicioIndexComponent,
    EstacionservicioAddComponent,
    EstacionservicioEditComponent,

    ],
    providers: [EstacionservicioService,MapsService,
      Candeactivateguard,
      {
        provide: HTTP_INTERCEPTORS,
        useClass: RequestInterceptor,
        multi: true,
      }]
})
export class EstacionservicioModule { }



